.countdown {
  --901: #6b35e8;
  --801: #3f13a4;
  --701: #050505;
  --601: #595959;
  --501: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--701);
  font-family: "Poppins", sans-serif;
  overflow: hidden;
}
.countdown #time {
  display: flex;
  gap: 30px;
  transform: scale(75%);
}
.countdown #time .circle {
  position: relative;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.countdown #time .circle svg {
  position: relative;
  width: 150px;
  height: 150px;
  transform: rotate(270deg);
}
.countdown #time .circle svg circle {
  width: 100%;
  height: 100%;
  fill: transparent;
  stroke-width: 8;
  stroke: #282828;
  stroke-linecap: round;
  transform: translate(5px, 5px);
}
.countdown #time .circle svg circle:nth-child(2) {
  stroke: var(--clr);
  stroke-dasharray: 440;
  stroke-dashoffset: 440;
}
.countdown #time div {
  position: absolute;
  text-align: center;
  font-weight: 500;
  color: #fff;
  font-size: 1.5em;
}
.countdown #time div span {
  position: absolute;
  transform: translateX(-50%) translateY(-10px);
  font-size: 0.35em;
  font-weight: 300;
  letter-spacing: 0.1px;
  text-transform: uppercase;
}
.countdown #time .dots {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 1000;
}
.countdown #time .dots:before {
  content: "";
  position: absolute;
  top: -3px;
  width: 15px;
  height: 15px;
  background: var(--clr);
  border-radius: 50%;
  box-shadow: 0 0 20px var(--clr), 0 0 60px var(--clr);
}
.countdown .newYear {
  font-size: 8em;
  font-weight: 500;
  color: #fff;
  text-align: center;
  line-height: 0.6em;
  display: none;
}
.countdown .newYear span {
  font-size: 0.5em;
  font-weight: 300;
}

:root {
  --901: hsl(258, 80%, 56%);
  --801: hsl(258, 79%, 36%);
  --701: #050505;
  --600: hsl(0, 0%, 20%);
  --601: #595959;
  --501: #fafafa;
  --bg-body: hsl(0, 0%, 2%);
  --bg-body-light: hsl(0, 0%, 90%);
  --boxActive: hsla(283, 82%, 20%, 0.545);
  --boxActive-light: hsl(268, 100%, 32%);
  --boxBorder: hsla(0, 0%, 100%, 0.425);
  --boxHover: hsla(0, 0%, 100%, 0.06);
  --box-bg: hsl(0, 0%, 8%);
  --box-bg-light: hsl(0, 0%, 3%);
  --box-t: hsl(0, 0%, 60%);
  --card-title: hsl(0, 0%, 100%);
  --card-title-bg-lg: hsl(0, 0%, 0%);
  --card-bg-lg: hsla(0, 0%, 0%, 0.35);
  --dark-theme-btn: hsl(0, 0%, 100%);
  --dark-theme-btn-circle: hsl(0, 0%, 0%);
  --light-theme-btn: hsl(0, 0%, 0%);
  --light-theme-btn-circle: hsl(0, 0%, 100%);
}

.days__body {
  font-family: "Roboto", sans-serif;
  background-color: var(--701);
  min-height: 100vh;
  padding-inline: 85px 15px;
  padding-top: 56px;
}

header iframe {
  width: 100%;
  overflow: hidden;
}

aside,
* {
  scrollbar-width: none;
}
aside::-webkit-scrollbar,
*::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: hsla(0deg, 0%, 0%, 0);
}
aside::-webkit-scrollbar-thumb,
*::-webkit-scrollbar-thumb {
  background-color: hsla(0deg, 0%, 0%, 0);
}

aside {
  /*
  aside ↓↓
        ==> [div] .home-svg
        ||    ↓↓
        ||    ==> [a] ==> [svg] ==> [path]
        ||
        ==> [div] .days-container  ( DAY 1 to DAY 100 ) 
              ↓↓
              ==> [div] .day
                    ↓↓
                    ==> [div] .day-number
                          ↓↓
                          ==> [span] (DAY 1..2..3)
  */
  width: 78px;
  padding: 4px;
  height: calc(100% - 56px);
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--701);
  overflow: auto;
}

aside .home-svg {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--801);
  height: 64px;
  width: 64px;
  margin-bottom: 5px;
}
aside .home-svg svg {
  width: 24px;
}

.days-container {
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
  border-radius: 0;
}

.days-container:hover > .day::after {
  opacity: 1;
}

.day {
  cursor: pointer;
  height: 64px;
  width: 64px;
  display: flex;
  flex-direction: column;
  position: relative;
}

.day:hover::before {
  opacity: 1;
}

.day::before,
.day::after {
  content: "";
  height: 100%;
  width: 100%;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  transition: opacity 500ms;
}

.day::before {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxHover), transparent 40%);
  z-index: 3;
}

.day::after {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxBorder), transparent 30%);
  z-index: 1;
}

.day > .day-number {
  background-color: var(--box-bg);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 2;
}
.day > .day-number span {
  color: var(--box-t);
  font-weight: 600;
  font-size: 1rem;
  margin: auto;
  text-align: center;
}

.day > .active {
  background-color: var(--boxActive);
}

.sidebar-menu-open {
  width: 207px;
}
.sidebar-menu-open .days-container {
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2px;
}
.sidebar-menu-open .home-svg {
  width: 100%;
}

.sidebar-menu-open {
  animation: sidebar-open 200ms ease-in-out forwards;
}
@keyframes sidebar-open {
  0% {
    width: 78px;
  }
  100% {
    width: 207px;
  }
}

main section {
  display: block;
  min-height: 300px;
}

main .statistics-section a {
  gap: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
main .statistics-section a img {
  height: 130px;
  border-radius: 5px;
}

main .projects-update {
  background-color: hsla(273deg, 100%, 50%, 0.025);
  display: grid;
  justify-content: center;
  position: relative;
  padding: 20px 0;
}

article {
  display: flex;
  flex-direction: column;
}
article .project-title {
  display: flex;
  justify-content: center;
}
article .project-title h3 {
  text-shadow: 0 0 1px;
  color: hsl(0deg, 0%, 100%);
}
article .repo-info {
  padding: 20px 0 0 0;
  flex-wrap: wrap;
  gap: 5px;
}
article .repo-new-features {
  padding: 15px 0 10px 12px;
  color: hsla(0deg, 0%, 100%, 0.685);
}
article .repo-new-features iframe {
  display: block;
  margin-inline: auto;
}
@media (max-width: 800px) {
  article .repo-new-features iframe {
    width: 600px;
    height: 340px;
  }
}
article .repo-new-features .commit-info a {
  padding-left: 7px;
  color: hsla(315deg, 100%, 50%, 0.253);
}
article .repo-new-features h4 {
  color: hsla(0deg, 0%, 100%, 0.808);
  padding-block: 20px 7px;
}
article .repo-new-features ul {
  padding-left: 20px;
  list-style: none;
}
article .repo-new-features ul li {
  position: relative;
  padding-block: 3px;
}
article .repo-new-features ul li::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: solid 5px transparent;
  border-left-color: var(--901);
  top: 50%;
  transform: translateY(-50%);
  left: -10px;
}

.section-title-contanier {
  --purple: rgb(123, 31, 162);
  --violet: rgb(103, 58, 183);
  --pink: rgb(244, 143, 177);
  /* -- YouTube Link Styles -- */
}
@keyframes background-pan {
  from {
    background-position: 0% center;
  }
  to {
    background-position: -200% center;
  }
}
@keyframes scale {
  from, to {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
.section-title-contanier h2 {
  color: white;
  font-family: "Rubik", sans-serif;
  font-size: clamp(1.2em, 1.2vw, 2.4em);
  font-weight: 400;
  margin: 0;
  padding-block: 40px 15px;
  text-align: center;
}
.section-title-contanier h2 > .magic {
  display: inline-block;
  position: relative;
}
.section-title-contanier h2 > .magic > .magic-star {
  --size: clamp(10px, 0.75vw, 15px);
  animation: scale 700ms ease forwards;
  display: block;
  height: var(--size);
  left: var(--star-left);
  position: absolute;
  top: var(--star-top);
  width: var(--size);
}
.section-title-contanier h2 > .magic > .magic-star > svg {
  animation: rotate 1000ms linear infinite;
  display: block;
  opacity: 0.7;
}
.section-title-contanier h2 > .magic > .magic-star > svg > path {
  fill: var(--violet);
}
.section-title-contanier h2 > .magic > .magic-text {
  animation: background-pan 3s linear infinite;
  background: linear-gradient(to right, var(--purple), var(--violet), var(--pink), var(--purple));
  background-size: 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  white-space: nowrap;
}
.section-title-contanier #source-link {
  top: 60px;
}
.section-title-contanier #source-link > i {
  color: rgb(94, 106, 210);
}
.section-title-contanier #yt-link {
  top: 10px;
}
.section-title-contanier #yt-link > i {
  color: rgb(239, 83, 80);
}
.section-title-contanier .meta-link {
  align-items: center;
  backdrop-filter: blur(3px);
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: inline-flex;
  gap: 5px;
  left: 10px;
  padding: 10px 20px;
  position: fixed;
  text-decoration: none;
  transition: background-color 600ms, border-color 600ms;
  z-index: 10000;
}
.section-title-contanier .meta-link:hover {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.section-title-contanier .meta-link > i,
.section-title-contanier .meta-link > span {
  height: 20px;
  line-height: 20px;
}
.section-title-contanier .meta-link > span {
  color: white;
  font-family: "Rubik", sans-serif;
  transition: color 600ms;
}

