    grid-template-columns: 1fr;
  }
}
@media (min-width: 500px) {
  main .resources-section {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 750px) {
  main .resources-section {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (min-width: 1100px) {
