nav .top-navbar-left-contaner {
  height: 100%;
  width: 72px;
  display: flex;
  align-items: center;
  justify-content: center;
}
nav .top-navbar-left-contaner .light-dark-btn {
  width: 60px;
  height: 25px;
  background-color: var(--dark-theme-btn);
  border-radius: 50px;
  position: relative;
  transition: 3150ms background-color ease;
}
nav .top-navbar-left-contaner .light-dark-btn .circle {
  width: 20px;
  height: 20px;
  background-color: var(--dark-theme-btn-circle);
  border-radius: 100%;
  position: absolute;
  top: 3px;
  left: 3px;
  transition: 350ms transform ease;
}

.light-theme {
  background-color: var(--bg-body-light);
}
.light-theme .days-container .day > .active {
  background-color: var(--boxActive-light);
}
.light-theme .days-container .day-number {
  background-color: var(--box-bg-light);
}
.light-theme nav .top-navbar-left-contaner .light-dark-btn {
  background-color: var(--light-theme-btn);
}
.light-theme nav .top-navbar-left-contaner .light-dark-btn .circle {
  background-color: var(--light-theme-btn-circle);
  transform: translateX(32.5px);
}

nav .search-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  max-width: 600px;
}
nav .search-bar input {
  width: calc(100% - 45px);
  height: 34px;
  border: solid 1px var(--901);
  box-shadow: inset 0 0 1px 0 var(--901);
  border-radius: 30px 0 0 30px;
  padding: 5px 15px;
  background-color: hsla(0deg, 0%, 96%, 0.185);
  color: #fff;
}
nav .search-bar input:focus {
  color: #000;
  font-weight: 500;
  background-color: hsl(0deg, 0%, 90%);
  outline: none;
  box-shadow: inset 0 0 4px 1px var(--901);
}
nav .search-bar .search-bun {
  border-radius: 0 30px 30px 0;
  cursor: pointer;
  width: 45px;
  height: 34px;
  background-color: var(--901);
  display: flex;
  align-items: center;
  justify-content: center;
}
nav .search-bar .search-bun svg {
  width: 24px;
  height: 24px;
}

.hamburger-btn {
  --button-color: #3f0033;
  background: transparent;
  border: 0;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hamburger-btn .svg {
  border-radius: 10px;
  padding: 6px 9px;
  background-color: rgba(255, 255, 255, 0.233);
  cursor: pointer;
}
.hamburger-btn .svg:hover {
  background-color: rgba(234, 0, 255, 0.151);
}
.hamburger-btn img {
  width: 24px;
  height: 24px;
}